html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

main {
  padding: 0; }

body {
  margin: 0;
  padding: 0 !important;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  font-size: 13px;
  line-height: 1.6; }

* {
  box-sizing: inherit; }

ul, ol {
  padding-left: 15px;
  margin-bottom: 0; }

a {
  color: #0000ff;
  -webkit-transition: all 0.3s;
  transition: all 0.3s; }
  a:hover {
    text-decoration: none;
    color: #0000cf; }

img {
  width: 100%; }

p, h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  font-weight: 400;
  margin-bottom: 0; }
  .theme-default p, .theme-default h1, .theme-default h2, .theme-default h3, .theme-default h4, .theme-default h5, .theme-default h6 {
    color: #646777; }

p {
  margin: 10px 0 0 0; }
  p:first-child {
    margin: 0; }

h1 {
  font-size: 36px;
  line-height: 48px; }
  h1.subhead {
    font-size: 30px;
    color: #0f0f0f;
    line-height: 36px; }

h2 {
  font-size: 30px;
  line-height: 36px; }
  h2.subhead {
    font-size: 24px;
    color: #0f0f0f;
    line-height: 32px; }

h3 {
  font-size: 24px;
  line-height: 32px; }
  h3.subhead {
    font-size: 18px;
    color: #0f0f0f;
    line-height: 24px; }

h4 {
  font-size: 18px;
  line-height: 24px; }
  h4.subhead {
    font-size: 12px;
    color: #0f0f0f;
    line-height: 16px; }

h5 {
  font-size: 14px;
  line-height: 18px; }
  h5.subhead {
    font-size: 10px;
    color: #0f0f0f;
    line-height: 12px; }

h6 {
  font-size: 12px;
  line-height: 16px; }
  h6.subhead {
    font-size: 8px;
    color: #0f0f0f;
    line-height: 10px; }

.bold-text {
  font-weight: 700; }

.typography--inline {
  display: flex;
  flex-wrap: wrap; }
  .typography--inline * {
    margin-top: auto;
    margin-right: 15px; }

.typography-card h1, .typography-card h2, .typography-card h3, .typography-card h4, .typography-card h5, .typography-card h6 {
  margin-bottom: 10px; }

blockquote {
  font-size: 13px;
  font-style: italic;
  border-left: 3px solid #ccc;
  margin-bottom: 0;
  padding-left: 10px; }

.highlight {
  background-color: #ccc;
  color: #ffffff; }

.red-text {
  color: #ad4444; }

.page-title {
  font-weight: 500;
  text-transform: capitalize;
  font-size: 20px; }
  .page-title:last-child {
    margin-bottom: 30px; }
  .page-title.page-title--not-last {
    margin-top: 20px; }

.page-subhead {
  margin-bottom: 20px; }
  .page-subhead.subhead {
    font-size: 14px;
    opacity: 0.7; }

::-moz-selection {
  color: #ffffff;
  background: #ccc; }

::selection {
  color: #ffffff;
  background: #ccc; }

.load {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  position: fixed;
  background: #ffffff;
  z-index: 1000; }
  .load + div {
    height: 100vh;
    overflow: hidden; }
  .load.loaded {
    -webkit-animation: ease loaded 0.5s;
            animation: ease loaded 0.5s; }

.load__icon {
  -webkit-animation: linear load 2s infinite;
          animation: linear load 2s infinite;
  margin: auto;
  width: 32px;
  height: 32px; }

.load__icon-wrap {
  margin: auto; }

@-webkit-keyframes load {
  from {
    -webkit-transform: rotate(0deg) scale(2);
            transform: rotate(0deg) scale(2); }
  to {
    -webkit-transform: rotate(360deg) scale(2);
            transform: rotate(360deg) scale(2); } }

@keyframes load {
  from {
    -webkit-transform: rotate(0deg) scale(2);
            transform: rotate(0deg) scale(2); }
  to {
    -webkit-transform: rotate(360deg) scale(2);
            transform: rotate(360deg) scale(2); } }

@-webkit-keyframes loaded {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

@keyframes loaded {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

.game-cell {
  width: 100%;
  height: 30px;
  background-color: #238a1c;
  text-align: center;
  border: 1px solid #185e1a;
  cursor: pointer;
  font-size: 24px;
  color: #ffffff; }

.fill {
  background-color: #8b85f2;
  cursor: normal; }

.app {
  font-family: 'Poppins', sans-serif;
  width: 100%;
  background: #ffffff;
  position: absolute;
  z-index: 0;
  overflow-x: hidden; }
  .app .container {
    -webkit-transition: 0.3s;
    transition: 0.3s; }
  @media (min-width: 576px) {
    .app .container {
      max-width: 540px; } }
  @media (min-width: 768px) {
    .app .container {
      max-width: 720px; } }
  @media (min-width: 992px) {
    .app .container {
      max-width: 960px; } }
  @media (min-width: 1200px) {
    .app .container {
      max-width: 1140px; } }

.gameHeader {
  background: #4A752C;
  color: #fefefe;
  padding: 5px; }

@media screen and (min-width: 768px) {
  .container {
    width: 100%;
    max-width: 1630px; } }

